header {
    background-color: $myOrange;
    padding: 20px 40px;
    padding-top: 10px;
    border-radius: 3px;

    display: flex;
    justify-content: center;
    align-items: center;
	flex-direction: column;
}

img.card {
    height: 450px;
    width: calc(450px * 2 / 3);
	box-shadow: 1px 1px 12px -3px;
    border-radius: 5px;
    margin-top: 10px;

    &.bigCard {
        height: min(750px, 90vh);
        width: calc(min(750px, 90vh) * 2 / 3);
    }
}

/* --------------------------------- */
/* --------- DROPDOWN LIST --------- */
/* --------------------------------- */

#filterSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 15px;
    margin-bottom: 0;

    button {
        margin-bottom: 15px;
    }
}

.dropdownInput {
	display: block;
    height: calc(1.5em + .75rem + 2px);
    padding: .15rem .75rem;
    padding-top: .235rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
	transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

/* The container <div> - needed to position the dropdown content */
.dropdownList {
	position: relative;
	display: inline-block;
	margin: 15px;
}

// /* Dropdown Content (Hidden by Default) */
.dropdownContent {
	border-top: 1px solid #9C9C9C;
    border-radius: 4px;
	box-shadow: 0px 1px 16px -6px;
    
    width: 100%;

	// z-index: 1;
    position: absolute;
    
	display: none;
}

.showDropdown {
	display: block;
}
.dropdownContentElement {
    // font-family: 'Roboto', sans-serif;
    font-family: 'PT Serif', serif;
    font-size: 14.5px;
    
	border: 1px solid rgb(207, 207, 207);
	border-top: 0px;
	border-radius: 4px;
	text-align: center;
	background-color: white;
	color: black;
	cursor: pointer;
	padding-top: 9px;
	padding-bottom: 7px;
	&:hover {
        background-color: $myOrangeLighter;
	}
}

/* --------------------------------- */
/* -------- AUTHOR CREDITS --------- */
/* --------------------------------- */

.authorWrapper {
    display: flex;
    justify-content: center;
    
    margin-top: 7.5px;
}

.author {
    border: 1.5px solid $myOrange;
    background-color: $myOrangeLighter;
    padding: 9px;
    border-radius: 4px;
    font-family: 'PT Serif', serif;
    font-weight: normal;
}

.icon {
	width: 20px;
	font-size: 18px;
	margin-left: 2px;
	vertical-align: middle;
}
a:hover, a:visited, a:link, a:active {
	&.icon {
		text-decoration: none;
	}
}

.cardWrapper {
	margin: 5px;
	display: flex;
	flex-direction: column;
}

.cardsList {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

button {
    cursor: pointer;
    border-radius: 3px;
    
    margin: 5px auto;
    width:  40px;
    height: 40px;
    padding: 0;

    font-size: 25px;
    text-align: center;
    text-justify: center;
    font-weight: bold;

    color: rgb(0, 0, 0);
    background-color: $myOrangeLighter;
    border: 1px solid $myOrange;
    box-shadow: 3px 3px 5px -3px black;

    &:hover {
        color: white;
        background-color: $myOrange;
    }
}

